<template>
  <div class="bem-popup" v-if="show">
    <div v-if="mask" class="bem-popup__mask" @clickMask="handleClickMask"></div>
    <div class="bem-popup__main" :style="style">
      <div class="bem-popup__header" :style="center ? {'text-align':'center'} : ''">
        <slot name="title">
          <span class="bem-popup__title">{{ title }}</span>
        </slot>
        <img
          v-if="showClose"
          class="bem-popup__close"
          @click="close"
          src="../../../assets/imgs/close_icon.png"
        />
      </div>
      <div class="bem-popup__body">
        <slot></slot>
      </div>
      <div class="bem-popup__footer" v-if="$slots.footer">
        <slot name="footer"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BemPopup",
  data() {
    return {};
  },
  computed: {
    style() {
      let style = {};
      style.marginTop = this.top;
      style.width = this.width;
      return style;
    }
  },
  methods: {
    handleClickMask() {
      if (!this.closeOnClickMask) this.close();
    },
    close() {
      this.$emit("update:show", false);
      this.$emit("close");
    }
  },
  props: {
    // 弹窗宽度
    width: {
      type: String,
      default: "50%"
    },
    // 弹窗距离顶部距离
    top: {
      type: String,
      default: ""
    },
    // 是否全屏显示
    fullscreen: {
      type: Boolean,
      default: false
    },
    // 标题
    title: {
      type: String,
      default: ""
    },
    //标题是否居中
    center: {
      type: Boolean,
      default: false
    },
    show: {
      type: Boolean,
      required: true
    },
    // 是否显示遮罩
    mask: {
      type: Boolean,
      default: true
    },
    // 是否可以通过点击 mask 关闭
    closeOnClickMask: {
      type: Boolean,
      default: false
    },
    // 是否显示遮罩
    mask: {
      type: Boolean,
      default: true
    },
    // 是否显示关闭按钮
    showClose: {
      type: Boolean,
      default: true
    }
  }
};
</script>

